.main-a-propos {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 720px;
    max-width: 1440px;
    margin: 80px auto;

    @media screen and (max-width: 1160px) {
        display: block; 
        text-align: center;
        margin: 300px auto 80px;
    }
    @media screen and (max-width: 960px) {
        display: block; 
        text-align: center;
        margin: 100px auto 0px;
    }
    @media screen and (max-width: 580px) {
        margin: 100px auto 0px;
    }

    .title {
        width: 50%;
        color: $colortext;

        @media screen and (max-width: 1160px) {
            margin: 0 auto;
            width: 100%;
        }
       

        h1 {
            color: $colorhover;
            font-size: 64px;
        }
        h2 {
            font-size: 40px;
            margin-top: 20px;
            @media screen and (max-width: 450px) {
                font-size: 30px;
            }
        }
        p {
            font-size: 20px;
            width: 70%;
            margin-top: 20px;
            @media screen and (max-width: 1160px) {
                margin: 20px auto;
                width: 90%;
            }
        }
    }
    .img-container img {
        
        @media screen and (max-width: 580px) {
            width: 400px;
        }
        @media screen and (max-width: 440px) {
            width: 350px;
        }
        @media screen and (max-width: 380px) {
            width: 300px;
        }
    }
}
.about-me {
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 960px) {
        display: block;
        text-align: center;
        width: 100%;
    }
    @media screen and (max-width: 580px) {
        margin: -80px auto 80px;
    }

    img {
        margin-right: 30px;
    }
    p {
        width: 70%;
        color: $colortext;
        font-size: 24px;
        line-height: 1em;
        @media screen and (max-width: 960px) {
            display: block;
            text-align: center;
            margin: 20px auto;
            width: 90%;
        }
        @media screen and (max-width: 580px) {
            font-size: 20px;
        }
    }
}
.skills {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 100px 0 100px 0;
    overflow: hidden;

    h1 {
        color: $colorhover;
        margin-bottom: 30px;
        font-size: 32px;
    }
    .cards-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;

        .cards {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            margin: 20px;
            background: #21201D;
            padding: 20px;
            border-radius: 10px;

            &:hover {
                transform: scale(1.1);
                transition: transform 0.3s ease;
            }
            
            span {
                color: rgb(236, 236, 236);
                font-family: Arial, Helvetica, sans-serif;
                font-size: 32px;
                font-weight: lighter;
                margin-top: 10px;
            }
        }
        .cards:nth-child(4) {
            img {
                width: 148px;
                height: 128px;
            }
        }
    }
}
.projets {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;

    h1 {
        color: $colorhover;
        font-size: 32px;
        margin-bottom: 30px;
    }
    .projects-container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 100px;
        
        .card-project {
            position: relative;
            width: 400px;
            height: 250px;
            margin: 20px;

            @media screen and (max-width: 440px) {
                width: 350px;
            }
            &:hover::after {
                 background: rgba(0, 0, 0, 0.923);
                 transform: scale(1.05);
                 
                 transition: all 0.3s ease-in-out;
            }
            &::after {
                content: "";
                position: absolute;
                z-index: 1;
                width: 100%;
                height: 100%;
                background: rgba(0, 0, 0, 0.8);
            }
           img {
            position: absolute;
            width: 100%;
            height: 100%;
            } 

            h2 {
                color: $colorhover;
                position: absolute;
                font-size: 30px;
                font-weight: bolder;
                top: 20px;
                left: 50%;
                z-index: 1000;
                transform: translateX(-50%);

                @media screen and (max-width: 440px) {
                    font-size: 26px;
                }

            }
            span {
                color: $colortext;
                position: absolute;
                font-size: 16px;
                font-family: Arial, Helvetica, sans-serif;
                top: 70px;
                left: 50%;
                z-index: 1000;
                transform: translateX(-50%);

                &::after {
                    content: "";
                    position: absolute;
                    width: 100%;
                    height: 1px;
                    background: $colorhover;
                    top: 25px;
                    left: -50%;
                    transform: translateX(50%);
                }
            }
            p {
                color: $colortext;
                position: absolute;
                font-size: 15px;
                font-family: font1;
                top: 120px;
                text-align: center;
                width: 90%;
                left: 50%;
                z-index: 1000;
                transform: translateX(-50%);
            }
            button {
                color: $colortext;
                position: absolute;
                font-size: 15px;
                font-family: font1;
                top: 195px;
                z-index: 1000;
                background: #21201D;
                border: none;
                cursor: pointer;
                text-align: center;
                left: 50%;
                transform: translateX(-50%);
                padding: 10px 20px;

                &:hover {
                    transform: scale(0.9);
                    transform: translateX(-50px);
                    transition: all 0.4s ease-in-out;
                }

                a {
                    text-decoration: none;
                    color: $colortext;

                    &:hover {
                        color: $colorhover;
                        transition: all 0.3s ease-in-out;
                    }

                }
            }
        }

        
    }
}
.contact {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 100px;

    h1 {
        color: $colorhover;
        font-size: 32px;
    }
    .form-container form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 50px 0 100px;

        @media screen and (max-width: 410px) {
            width: 80%;
            margin: 50px auto 100px;
        }

        label {
            font-family: font1;
            color: $colortext;
            font-size: 30px;
        }
        input {
            background: transparent;
            border: none;
            border-bottom: 1px solid $colortext;
            outline: none;
            color: $colortext;
            padding: 10px;
            margin-bottom: 40px;
            font-size: 25px;
            width: 80%;
        }
        #mess {
            background: transparent;
            border: none;
            border-bottom: 1px solid $colortext;
            outline: none;
            color: $colortext;
            padding: 10px;
            margin-bottom: 20px;
            font-size: 25px;
            width: 80%;
        }
        input[type = 'submit'] {
            background: $colortext;
            color: black;
            font-family: font1;
            cursor: pointer;
            border: none;
            border-radius: 20px;
            padding: 10 40px;
            font-weight: bold;
            transition: all 0.3s ease-in-out;

            &:hover {
                background: $colorhover;
                
            }
        }
    }
    .formMessage {
        color: $colortext;
        height: 20px;
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: -100px;
        font-size: 30px;
    }
}
.footer {
    position: relative;
    
    span {
        color: $colortext;
        text-align: right;
        position: absolute;
        bottom: 20px;
        right: 20px;
        font-family: font1;
    }
    .social-network {
        position: absolute;
        bottom: 20px;
        left: 20px;
        width: 20%;
        height: 50px;
        font-size: 1.2rem;

        @media screen and (max-width: 650px) {
            left: 50%;
            transform: translateX(-50%);
            bottom: 40px;
        }

        ul {
            display: flex;
            width: 100%;
            min-width: 100px;
            justify-content: space-around;
            
            a {
            padding: 1rem;
            transition: 1.1s ease-out;
            color: $colortext; 
            }
        }

        
    }
}