@font-face {
    font-family: "font1";
    src: url(../assets/font/Inconsolata/Inconsolata-Regular.ttf);
}



$colorfond: #1B1A17;
$colortext: #F0E3CA;
$colorhover: #FF8303;



*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
a, a:visited, li {
    list-style-type: none;
}
body {
    font-family: "font1";
    background: $colorfond;
}