nav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background: #1b1a17e1;
    z-index: 2000;

    @media screen and (max-width: 960px) {
        height: 0px;
    }
    
    .liste {
        list-style-type: none;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        background: #1b1a17e1;
        
        @media screen and (max-width: 960px) {
            flex-direction: column;
            height: 100vh;
        }

        .items {
            margin: 80px;
            cursor: pointer;
            color: $colortext;
            font-weight: bold;
            font-size: 20px;
            position: relative;

            &::before {
                content: "";
                position: absolute;
                background: $colorhover;
                width: 100%;
                height: 1px;
                bottom: -3px;
                transform-origin: 100% 0;
                transform: scaleX(0);
                transition: transform 0.4s;
            }

            &:hover::before {
                transform-origin: 0 0;
                transform: scaleX(1);
            }
            
            @media screen and (max-width: 960px) {
                font-size: 30px;
                margin: 50px;
            }

            &:hover {
                color: $colorhover;
                transition: all 0.2s ease-in-out;
            }
            
        }
    }
    .box {
        display: none;
        position: absolute;
        width: 40px;
        height: 40px;
        border: none;
        border-radius: 5px;
        right: 10px;
        top: 10px;
        padding: 5px;
        color: black;
        background: $colortext;
        font-size: 18px;
        cursor: pointer;
        &.active .ligne:nth-child(1) {
            transform: rotate(45deg);
        }
        &.active .ligne:nth-child(2) {
            opacity: 0;
        }
        &.active .ligne:nth-child(3) {
            
            transform: rotate(-45deg);
        }
        @media screen and (max-width: 960px) {
            display: block;
        }
        .container-lignes {
            width: 30px;
            height: 21px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            pointer-events: none;

            .ligne {
                position: absolute;
                border-radius: 10px;
                width: 30px;
                height: 3px;
                background: black;

                &:nth-child(1) {
                    top: 0px;
                    transform: rotate(0deg);
                    transition: 
                    transform 0.4s ease-in-out,
                    top 0.4s 0.3s ease-in-out;
                }
                &:nth-child(2) {
                    top: 9px;
                    transition: opacity 0.4s 0.3s ease-in-out;
                }
                &:nth-child(3) {
                    top: 18px;
                    transform: rotate(0deg);
                    transition: 
                    transform 0.4s ease-in-out,
                    top 0.4s 0.3s ease-in-out;
                }
            }
        }
    }
    .box {
        &.active .ligne:nth-child(1) {
            transform: rotate(45deg);
            top: 9px;
            transition: 
            top 0.4s ease-in-out,
            transform 0.4s 0.4s ease-in-out;
        }
        &.active .ligne:nth-child(2) {
            transition: opacity 0.4s ease-in-out;
           opacity: 0;
        }
        &.active .ligne:nth-child(3) {
            transform: rotate(-45deg);
            top: 9px;
            transition: 
            top 0.4s ease-in-out,
            transform 0.4s 0.4s ease-in-out;
        }
    }
}


